.App-logo {
  padding: 25px;
  height: 5vmin;  
  pointer-events: none;
  z-index: 10;
  position: absolute;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}
.btn{
  width: 100px;
  height: 50px;
  border-radius: 50px;
  background-color:#61dafb;
  color: white;
  cursor: pointer;
  transition: ease-in-out .3s;
}
.btn:hover{
    opacity: .5;
  }


.form {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 20px;
  font-size: calc(10px + 2vmin);
  color: white;
}
input{
  display: flex;
  height: 30px;
  width: 300px;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(3000deg);
  }
}
